section.bookmark-section {
  margin-top: 2.5rem;
  margin-bottom: 9rem;
  width: clamp(28rem, 98%, 83.5rem);
  padding: 0 1.8rem;
}

section.empty-section {
  width: clamp(28rem, 100%, 83.5rem);
  height: 85rem;
}

div.empty-bookmark-container {
  width: clamp(20rem, 20vw, 36.9rem);
  height: clamp(18.407rem, 18.407vw, 26.1rem);
  margin: 0 auto;
  margin-top: 14.8rem;
}

.empty-bookmark-container img {
  width: clamp(15.074rem, 15.074vw, 23.448rem);
  height: clamp(12.407rem, 12.407vw, 19.3rem);
}

.empty-bookmark-container h2 {
  height: clamp(2.2rem, 2.2vw, 3rem);
  font-size: clamp(1.4rem, 1.4vw, 2.5rem);
  font-weight: var(--small-fw);
  line-height: clamp(2.194rem, 2.194vw, 3.48rem);
  letter-spacing: 2%;
  color: #407bffe5;
  text-align: center;
}

h2.bookmark-head {
  width: 12.5rem;
  height: 3.3rem;
  font-size: 2.2rem;
  font-weight: var(--small-fw);
  line-height: 3.3rem;
}

@media (min-width: 768px) {
  section.bookmark-section {
    margin-top: 1rem;
    margin-left: 0.9rem;
    margin-bottom: 1rem;
    padding: 0 1.5rem;
    border: 0.1rem solid #00000080;
    border-bottom: none;
    border-radius: 1.2rem 1.2rem 0rem 0rem;
    height: 100vh;
  }

  section.empty-section {
    height: 92rem;
    margin-top: 1rem;
    margin-left: 0.9rem;
    padding: 0 1.5rem;
    border: 0.1rem solid #00000080;
    border-radius: 1.2rem;
  }

  div.empty-bookmark-container {
    margin: unset;
    margin-top: unset;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
