section.query-section {
  max-width: 80.1rem;
}

ul.query-list {
  display: flex;
  justify-content: space-between;
  max-width: 50rem;
  margin-top: 2.2rem;
  margin-left: 1.9rem;
  margin-right: 1.9rem;
}

li.query-list-item {
  height: 2.2rem;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 2.194rem;
  letter-spacing: 2%;
}

hr.query-line-break {
  /* margin-top: 1.7rem; */
  border: 0.1rem solid #99999980;
}

@media (min-width: 768px) {
  section.query-section {
    border: 0.1rem solid rgba(0, 0, 0, 0.5);
    border-radius: 1.2rem 1.2rem 0rem 0rem;
    border-bottom: none;
    margin-top: 1.1rem;
    margin-left: 0.9rem;
    margin-top: 1.1rem;
    margin-left: 0.9rem;
    min-height: 100vh;
  }
}
